@media only screen and (max-width: 992px) {
  .navigation {
    nav {
      .menu-list {
        li {
          padding-right: 7%;
        }
      }
    }
  }
  main {
    section {
      .owl-carousel {
        .item {
          img {
            height: auto;
          }
        }
      }
    }
  }
  .info-block {
    .sect2 {
      border: 0;
      .button {
        margin-right: 0;
      }
      h3 {
        padding-left: 0;
      }
      .text {
        padding-left: 0;
        padding-right: 0;
      }
    }
    .sect3 {
      border-top: 1px solid #cccccc;
      padding-top: 20px;
      .left {
        float: left;
      }
      .right {
        float: right;
      }
      .tel,
      .email {
        border-bottom: 0;
      }
    }
  }
  .clients {
    padding-top: 20px;
    h2 {
      font-family: "Helvetica";
      font-weight: 500;
      font-size: 18px;
      color: #565656;
    }
    h2::before,
    h2::after {
      width: 0;
      height: 0;
    }
    p {
      padding-right: 10%;
    }
  }
  footer {
    .logo {
      .circle {
        .circle2 {
          left: 12px;
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .row {
    padding-left: 0;
    padding-right: 5px;
  }
  header {
    .logo {
      margin-top: 15px;
      margin-bottom: 0;
      .circle {
        .circle1,
        .circle2 {
          width: 46px;
          height: 46px;
        }
        .circle2 {
          left: 14px;
        }
      }
      .text {
        font-size: 26px;
      }
    }
    .text-block {
      margin-bottom: 10px;
      padding-left: 5%;
      h1 {
        font-size: 16px;
      }
    }
  }
  .navigation {
    nav {
      .menu-list {
        li {
          padding-right: 5%;
          a {
            font-size: 14px;
          }
        }
      }
    }
  }
  .clients {
    p {
      padding-right: 8%;
    }
  }
  footer {
    .logo {
      .text {
        left: 52%;
      }
    }
  }
}

@media only screen and (max-width: 576px) {
  .row {
    padding-right: 0;
    }
  body.lock {
    overflow: hidden;
    }
  header {
    .logo {
      text-align: center;
    }
    .hamburger-button {
      display: block;
    }
    .hamburger-list.active {
      display: inline-block;
    }
    .text-block {
      padding: 0;
      border: 0;
      text-align: center;
    }
  }
  .none {
    display: none;
  }
  .navigation {
    nav {
      display: none;
    }
  }
  main {
    h2 {
      padding-top: 10px;
    }
  }
  .info-block {
    .sect3 {
      padding-right: 0;
      .tel,
      .email,
      .address {
        text-align: center;
      }
    }
  }
  .clients {
    p {
      padding-left: 10%;
      padding-right: 10%;
    }
  }
  footer {
    .logo {
      .circle {
        left: 40%;
        .circle1,
        .circle2 {
          width: 24px;
          height: 24px;
        }
        .circle2 {
          left: 10px;
        }
      }
      .text {
        left: 45%;
        font-size: 16px;
      }
    }
    nav {
      li {
        border: none;
        padding-left: 5%;
      }
    }
  }
}
