@import "normalize";
@import "fonts";

$orange: #f2ac04;
$gray: #565656;

* {
  margin: 0px;
  padding: 0px;
}

.row {
  text-align: center;
  padding-left: 48px;
  padding-right: 35px;
}

a {
  text-decoration: none;
}

body {
  a:hover,
  a:active {
    color: #979797;
  }
}

.button {
  font-family: "Tahoma";
  font-size: 12px;
  background-color: #2b2b29;
  color: white;
  float: right;
}

.logo {
  position: relative;
  margin-bottom: 38px;
  height: 52px;
  .text {
    display: inline-block;
    float: left;
    font-size: 34px;
    font-family: "Helvetica", sans-serif;
    padding: 10px 37px 0 25px;
    margin-left: 73px;
    color: #3c3c3c;
  }
  .circle {
    float: left;
    .circle1,
    .circle2 {
      width: 56px;
      height: 56px;
      border-radius: 50%;
      position: absolute;
    }
    .circle1 {
      background-color: #f8d277;
      z-index: 10;
    }
    .circle2 {
      background-color: $orange;
      z-index: 20;
      left: 18px;
    }
  }
}

header {
  padding-top: 20px;
  .hamburger-button {
    display: none;
    width: 35px;
    height: 35px;
    background-color: transparent;
    border: none;
    position: fixed;
    right: 15px;
    top: 40px;
    z-index: 1000;
    overflow: hidden;
    span {
      display: block;
      position: absolute;
      background-color: $orange;
      height: 5px;
      width: 35px;
      top: 15px;
      &::after,
      &::before {
        display: block;
        position: absolute;
        background-color: $orange;
        height: 5px;
        width: 35px;
        content: "";
      }
      &::after {
        bottom: 10px;
      }
      &::before {
        top: 10px;
      }
    }
  }
  .hamburger-button-line {
    span {
      &::after {
        transition: transform 0.2s ease-in, bottom 0.2s linear 0.2s;
      }
      &::before {
        transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
      }
    }
  }
  .hamburger-button-line.active {
    span {
      width: 0;
      &::after,
      &::before {
        transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
      }
      &::after {
        bottom: 0;
        transform: rotate(45deg);
      }
      &::before {
        top: 0;
        transform: rotate(-45deg);
      }
    }
  }
  nav {
    .hamburger-list {
      width: 100%;
      height: 100%;
      position: fixed;
      background-color: #0d0d0d;
      opacity: 0.9;
      z-index: 900;
      padding-top: 40px;
      top: 0;
      left: 0;
      overflow: auto;
      display: none;
      li {
        list-style-type: none;
        padding: 10px;
        border-bottom: 1px solid #afafaf;
        margin-right: 75px;
        a {
          font-size: 18px;
          color: #ffffff;
          font-family: "Helvetica";
        }
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  .text-block {
    border-left: 1px solid #cac8c5;
    padding-left: 37px;
    text-align: left;
    h1 {
      font-family: "Novecento", sans-serif;
      font-size: 20px;
      font-weight: 300;
      color: $orange;
    }
    p {
      font-family: "MyriadPro", sans-serif;
      font-size: 14px;
      color: #929292;
    }
  }
}

.navigation {
  hr {
    border-top: 1px solid #ababab;
  }
  nav {
    .menu-list {
      text-align: left;
      li {
        list-style: none;
        display: inline-block;
        padding: 15px 9% 15px 0;
        a {
          font-family: "Swiss721", sans-serif;
          font-size: 18px;
          font-weight: 100;
          text-transform: uppercase;
          color: #6b6b6b;
        }
        a:hover,
        a:active {
          color: #979797;
        }
      }
    }
  }
}

main {
  h2 {
    font-family: "Tahoma";
    font-size: 12px;
    text-transform: uppercase;
    padding-top: 40px;
    padding-bottom: 10px;
    text-align: left;
    color: $gray;
  }
  section {
    border: 1px solid #d4d4d4;
    .owl-carousel {
      .item {
        img {
          height: 250px;
          width: 100%;
        }
      }
      .owl-nav {
        position: relative;
        margin: 0;
        .owl-prev,
        .owl-next {
          position: absolute;
          top: 5px;
          color: $orange;
        }
        .owl-prev {
          left: 10px;
        }
        .owl-next {
          right: 10px;
        }
      }
    }
    h4 {
      padding-top: 14px;
      padding-bottom: 14px;
      font-family: "Tahoma";
      font-size: 14px;
      font-weight: normal;
      color: $gray;
    }
  }
  hr {
    border-top: 1px solid #b0b0b0;
    margin-top: 2px;
  }
  article {
    .img {
      min-height: 199px;
      background: url("../imgs/article1.jpg") center;
      background-size: 100%;
      background-repeat: no-repeat;
      background-color: $orange;
    }
    hr {
      margin-top: 7px;
      margin-bottom: 15px;
      border-top: 1px solid black;
    }
    .text {
      text-align: left;
      font-family: "Helvetica";
      font-size: 16px;
      line-height: 1.5;
      font-stretch: condensed;
    }
  }
}

.info-block {
  hr {
    border-top: 1px solid #cccccc;
    margin-top: 24px;
    margin-bottom: 30px;
  }
  .sect1,
  .sect2,
  .sect3 {
    text-align: left;
    h3 {
      font-family: "Helvetica";
      font-weight: 500;
      font-size: 18px;
      text-transform: uppercase;
      a {
        color: $gray;
      }
    }
  }
  .sect1 {
    padding-right: 10px;
    padding-bottom: 12px;
    .news {
      border-bottom: 1px dashed #636363;
      padding-top: 20px;
      padding-bottom: 14px;
      .date {
        float: left;
        font-family: "CondMono";
        color: $gray;
        text-transform: uppercase;
        font-size: 11px;
        border-right: 1px solid #c7c7c7;
        padding: 2px 15px 2px 5px;
        span {
          color: $orange;
          font-size: 18px;
        }
      }
      h4 {
        font-family: "MyriadPro";
        font-size: 16px;
        a {
          padding-left: 10px;
          color: $gray;
        }
      }
      .text {
        font-family: "Tahoma";
        font-size: 12px;
        color: #939393;
        padding-left: 70px;
      }
    }
  }

  .sect2 {
    border-left: 1px solid #e5e5e5;
    border-right: 1px solid #e5e5e5;
    padding-bottom: 40px;
    h3 {
      padding-left: 35px;
    }
    .text {
      font-family: "Helvetica";
      font-size: 16px;
      line-height: 1.5;
      padding: 38px 47px 20px 35px;
    }
    .button {
      margin-right: 47px;
      margin-bottom: 40px;
    }
  }

  .sect3 {
    padding-right: 50px;
    h3 {
      padding-bottom: 20px;
    }
    .tel,
    .email {
      border-bottom: 1px dashed #999999;
    }
    .tel,
    .email,
    .address {
      padding-top: 10px;
      padding-bottom: 12px;
    }
    .tel {
      font-family: "Swiss721";
      font-weight: 900;
      font-size: 16px;
      color: #979797;
    }
    .email,
    .address {
      font-family: "MyriadPro";
      font-size: 16px;
      color: #757575;
    }
  }
}

.clients {
  h2 {
    color: #343434;
    font-family: "MyriadPro";
    font-size: 16px;
    text-transform: uppercase;
    text-align: left;
    &::before {
      content: "";
      width: 5%;
      height: 1px;
      background-color: #cccccc;
      display: inline-block;
      vertical-align: middle;
      margin-right: 5px;
    }
    &::after {
      content: "";
      width: 83%;
      height: 1px;
      background-color: #cccccc;
      display: inline-block;
      vertical-align: middle;
      margin-left: 5px;
    }
  }
  p {
    display: inline-block;
    font-size: 40px;
    color: #bcbcbc;
    padding-right: 12%;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  hr {
    border-top: 1px solid #cccccc;
  }
}

footer {
  padding-top: 25px;
  padding-bottom: 20px;
  .logo {
    text-align: center;
    margin: 0;
    .circle {
      position: absolute;
      left: 45%;
      .circle1,
      .circle2 {
        width: 34px;
        height: 34px;
      }
      .circle2 {
        left: 10px;
      }
    }
    .text {
      font-size: 20px;
      float: none;
      margin-left: 0;
      padding-top: 6px;
      padding-bottom: 6px;
      position: absolute;
      left: 50%;
    }
  }
  nav {
    li {
      list-style: none;
      display: inline-block;
      border-right: 1px solid #bbbbbb;
      padding-left: 1%;
      padding-right: 1%;
      &:last-child {
        border-right: 1px solid transparent;
      }
      a {
        font-family: "Swiss721", sans-serif;
        font-size: 12px;
        text-transform: uppercase;
        color: $gray;
      }
    }
  }
}

@import "media";
